.nemid-container {
  position: relative;
  font-family: Arial;
  font-size: 12px;
  border: 3px solid #ccc;
  width: 220px;
}

img.nemid-logo {
  height: 20px;
  margin-left: 5px;
  margin-top: 5px;
}

.site-name {
  padding: 5px;
  background: #ddd;
  font-size: 14px;
}

.content-container {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#edf2f1), to(#fefefe));
  background-image: -webkit-linear-gradient(top, #edf2f1 0%, #fefefe 100%);
  background-image: -o-linear-gradient(top, #edf2f1 0%, #fefefe 100%);
  background-image: linear-gradient(to bottom, #edf2f1 0%, #fefefe 100%);
  height: 220px;
  padding: 5px;
}

.content-container.papkort {
  padding: 10px 25px;
}

.content-container.papkort table {
  table-layout: fixed;
  width: 100%;
  border: 1px solid #bccccb;
  background-color: #ebf2f1;
}

.content-container.papkort thead {
  border-bottom: 1px solid #bccccb;
}

.content-container.papkort table td {
  width: 50%;
  text-align: center;
}

.content-container.papkort input.otp-response-code {
  text-align: center;
  width: 50px;
  margin-top: 10px;
}

.content-container .header {
  font-size: 14px;
  font-weight: bold;
}

.input-container {
  margin-bottom: 10px;
}

.input-container input {
  border: 1px solid #bccccb;
  outline: 0;
  padding: 0 0 0 2px;
  color: #000;
  height: 20px;
  font-size: 12px;
  border-radius: 2px;
}

.bottom {
  position: absolute;
  bottom: 10px;
}

.bottom .forgotten-password {
  cursor: pointer;
  text-decoration: underline;
  color: #0d9395;
  margin-bottom: 5px;
}

.content-container .submit-button {
  height: 20px;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#f2f2f2), to(#fff));
  background-image: -webkit-linear-gradient(bottom, #f2f2f2 0%, #fff 100%);
  background-image: -o-linear-gradient(bottom, #f2f2f2 0%, #fff 100%);
  background-image: linear-gradient(0deg, #f2f2f2 0%, #fff 100%);
  cursor: pointer;
  width: 100%;
  border: 1px solid #a4b3b2;
  border-radius: 2px;
  color: #0d9395;
}

.submit-button:hover,
.input-container input:hover,
.input-container input:focus {
  border: 1px solid #095f61;
}

.nemid-container.is-loading .loading-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ebf2f1), to(#fff));
  background-image: -webkit-linear-gradient(top, #ebf2f1 0%, #fff 100%);
  background-image: -o-linear-gradient(top, #ebf2f1 0%, #fff 100%);
  background-image: linear-gradient(to bottom, #ebf2f1 0%, #fff 100%);
  opacity: 0.9;
  z-index: 1;
}

.nemid-container.is-loading .loading-indicator {
  background-image: url(data:image/gif;base64,);
  background-position: center center;
  background-repeat: no-repeat;
  height: 100%;
  z-index: 2;
}

.icon-phone-container {
  border: 1px solid #ccc;
  padding: 5px;
  text-align: center;
}

/* ICONS */
.icon-phone {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}

.icon-hash::before {
  content: url(data:image/svg+xml;base64,);
  height: 12px;
  width: 12px;
  display: inline-block;
  margin-right: 2px;
  margin-left: 2px;
  vertical-align: middle;
}

.icon-key::before {
  content: url(data:image/svg+xml;base64,);
  height: 8px;
  width: 20px;
  display: inline-block;
  margin-right: 3px;
  margin-left: 3px;
  vertical-align: top;
}




